      key: 1,
      buttonName: '全选',
      addState: true,
      defaultPj: '请选择配件',
      indexs: '',
      isShow: false,
      defaultImg: 'this.src="' + require('@/assets/no-img.png') + '"',
      defaultImg1: require('@/assets/images/tx.png'),
      dbts: '加入对比',
      cgsl: 1, // 购买数量
      xzGys: '',
      num: 0,
      showMore: false,
      showMoreAccessory: false,
      accessoryList: [],
      accessoryCheck: [],
      accessoryId: [],
      accessoryPrice: [],
      accessoryName: [],
      defaultGys: '请选择供应商', // 默认供应商
      gysList: [], // 供应商列表
      show2: false,
      cjjlList: [], // 成交记录信息
      ggbz: '', // 规格包装
      pro_intro: '', // 商品介绍
      myfavoriteList: [], // 浏览历史商品详情列表
      comparisionProductList: [], // 相似商品列表
      defaultAddress: '请选择地址', // 默认收获地址
      addresList: [], // 所有收货地址
      spxx: '', // 商品信息
      orderDetailList: [], // 所有订单评价列表
      orderDetailListShow: [], // 当前页展示订单评价列表
      active: '',
      activeStep: 0, // 用作添加点击后的类名
      flag: false, // true是固定定位
      btnScrollTop: 0, // 导航条距离顶部的距离
      current: 0,
      slide: [],
      images: [
        'https://img.yzcdn.cn/vant/apple-1.jpg',
        'https://img.yzcdn.cn/vant/apple-2.jpg'
      ],
      show: false,
      show1: false,
      skuData: '',
      showBase: true,
      showCustom: false,
      showStepper: false,
      showSoldout: false,
      closeOnClickOverlay: true,
      initialSku: {
        s1: '30349',
        s2: '1193',
        selectedNum: 3
      },
      customSkuValidator: () => '请选择xxx!',
      spId: '',
      showGys: false,
      showSku: false,
      showChoose: false,
      showImage: false,
      indexImage: 0,
      countvalue: 1,
      checked: [],
      addressIndex: '',
      showActive: false,
      cart_id: '',
      address: {},
      gysmc: ''
    }
  },
  watch: {
    accessoryId() {
      if (this.accessoryId.length === this.accessoryList.length) {
        this.buttonName = '全不选'
      } else {
        this.buttonName = '全选'
      }
    }
  },
  mounted() {
    const btnScrollTop = document.querySelector('.operation-btn').offsetTop
    this.btnScrollTop = btnScrollTop // 挂载后获取到导航条距离顶部的距离，然后给data.btnScrollTop

    // 监听滚动条滚动的距离，超过导航条距离顶部的距离则添加相应类名，小于这删除相应类名
    document.addEventListener('scroll', () => {
      const target = document.documentElement.scrollTop
      this.btnScrollTop = target
      if (target >= this.btnScrollTop) {
        this.flag = true
      } else {
        this.flag = false
      }
    })
  },
  created() {
    this.spId = this.$store.state.goods.spid
    this.getlistComparisionDetail()
    this.getSpxx()
    this.getOrderDetail()
    this.getAdressList()
    this.getComparisionProductList()
    this.getMyfavoriteList()
    this.getSpjsAndGgbz()
    this.getAccessoryList()
    this.getProductList()
    this.getDelAddress()
  },
  activated() {
    if (this.$route.params.refresh) {
      this.spId = this.$store.state.goods.spid
      this.getlistComparisionDetail()
      this.getSpxx()
      this.getOrderDetail()
      this.getAdressList()
      this.getComparisionProductList()
      this.getMyfavoriteList()
      this.getSpjsAndGgbz()
      this.getAccessoryList()
      this.getProductList()
      this.getDelAddress()
    } else {
      this.getlistComparisionDetail()
      this.getAdressList()
    }
  },
  methods: {
    gmslChange(value) {
      if (value === '-') {
        this.cgsl = 1
      }
      if (value > 99999) {
        this.cgsl = 99999
        this.key++
      }
    },
    getDelAddress() {
      this.asyncApiModule(getDefAddress).then(res => {
        if (res.province) {
          this.address.province = res.province
        }
        if (res.city) {
          this.address.city = res.city
        }
        if (res.county) {
          this.address.county = res.county
        }
        if (res.town && res.town !== '1') {
          this.address.town = res.town
        }
      }).catch(() => {
        this.loading = false
      })
    },
    // 获取对比商品列表
    getlistComparisionDetail() {
      this.asyncApiModule(listComparisionDetail).then(res => {
        this.checked = []
        for (let i = 0; i < res.length; i++) {
          this.checked.push(res[i].id)
        }
        if (this.checked.indexOf(this.spId) === -1) {
          this.dbts = '加入对比'
          this.showActive = false
        } else {
          this.dbts = '取消对比'
          this.showActive = true
        }
      })
    },
    selectAll() {
      if (this.accessoryList.length === this.accessoryId.length) {
        for (let i = 0; i < this.accessoryCheck.length; i++) {
          this.accessoryCheck[i] = false
        }
        this.accessoryId = []
        this.accessoryName = []
        this.accessoryPrice = []
        this.defaultPj = '请选择配件'
        this.buttonName = '全选'
      } else {
        this.accessoryId = []
        this.accessoryName = []
        this.accessoryPrice = []
        this.defaultPj = ''
        for (let i = 0; i < this.accessoryList.length; i++) {
          this.accessoryId.push(this.accessoryList[i].id)
          this.accessoryName.push(this.accessoryList[i].name)
          this.accessoryPrice.push(this.accessoryList[i].price)
          this.defaultPj += this.accessoryList[i].name + ' '
        }
        for (let j = 0; j < this.accessoryCheck.length; j++) {
          this.accessoryCheck[j] = true
        }
        this.buttonName = '全不选'
      }
    },
    unSelectAll() {
      this.showSku = false
    },
    imgPreview(item) {
      const slide = []
      slide.push(item)
      ImagePreview({
        images: slide, // 预览图片的那个数组
        loop: false
      })
    },
    onChange1(index) {
      this.current = index
    },
    fd() {
      this.isShow = true
    },
    onChange(index) {
      this.indexs = index
    },
    // 加入对比
    async addCompare(product_id) {
      if (!product_id) {
        this.$toast('商品信息错误，加入对比失败')
        return
      }
      if (this.checked.indexOf(product_id) === -1) {
        if (this.checked.length >= 4) {
          this.$toast('对比栏已满，一次性最多可对比4件商品，建议您删除不需要的再添加哦！')
        } else {
          this.checked.push(product_id)
          addComparision.data = { e_product_id: null, product_id: product_id }
          this.asyncApiModule(addComparision).then(res => {
            this.$toast('加入对比成功')
            this.dbts = '取消对比'
            this.showActive = !this.showActive
          })
            .catch(() => {
            })
        }
      } else {
        this.checked.splice(this.checked.indexOf(product_id), 1)
        cancelComparision.data = { e_product_id: '', product_id: product_id }
        this.asyncApiModule(cancelComparision).then(res => {
          this.$toast('取消对比成功')
          this.dbts = '加入对比'
          this.showActive = !this.showActive
        })
          .catch(() => {
          })
      }
    },
    showPopupSp() {

    },
    onAddCartClicked() {

    },
    onBuyClicked() {

    },
    // 加入购物车
    async toCart(e_cart_id, spid) {
      if (!this.addState) {
        deleteCart.data = { e_cart_id: e_cart_id }
        this.asyncApiModule(deleteCart).then(res => {
          this.$toast.success('取消成功')
          this.num = 0
          this.addState = true
          this.getProductList()
        }).catch(() => {
          this.loading = false
        })
      } else {
        if (!spid) {
          this.$toast('商品信息错误，加入购物车失败')
          return
        }
        var productInfo = {
          province: this.address.province,
          city: this.address.city,
          county: this.address.county,
          town: this.address.town,
          produceList: [],
          dsbs: this.spxx.dsbs
        }
        productInfo.produceList.push({
          skuId: this.spId,
          num: 1
        })
        let state = true
        await this.$store.dispatch('goods/getLessStockAction', productInfo).then(res => {
          for (var key in res) {
            if (key !== 'success') {
              this.$toast(res[key])
              state = false
            }
          }
        }).catch(() => {
          this.loading = false
        })
        if (!state) {
          return
        }
        if (this.spxx.dsbs === 'KJXY' && this.xzGys.length === 0) {
          this.$toast('请选择供应商！')
          return
        } else {
          addCarts.data = { pcount: this.cgsl, product_id: this.spId, e_supplier_id: this.xzGys, accessoryIdsList: this.accessoryId }
          this.asyncApiModule(addCarts).then(res => {
            this.$toast.success('添加成功')
            this.num = 0
            this.addState = false
            this.getProductList()
          })
            .catch(() => {
              this.$toast.fail('添加失败')
              this.loading = false
            })
        }
      }
    },
    getProductList() {
      if (this.num !== 0) {
        return
      }
      this.asyncApiModule(getCartList).then(res => {
        for (let i = 0; i < res.cartsList.length; i++) {
          const item = res.cartsList[i]
          this.num = this.num + item.productList.length
          for (let j = 0; j < item.productList.length; j++) {
            const temp = item.productList[j]
            if (temp.id === this.spxx.id) {
              this.cart_id = temp.cart_id
            }
          }
        }
      })
        .catch(() => {
          this.loading = false
        })
    },
    changeAccessory(accessory_id, accessory_name, accessory_price, index) {
      if (this.accessoryCheck[index]) {
        this.accessoryCheck.splice(index, 1, false)
        this.accessoryId.splice(this.accessoryId.indexOf(accessory_id), 1)
        this.accessoryName.splice(this.accessoryName.indexOf(accessory_name), 1)
        this.accessoryPrice.splice(this.accessoryPrice.indexOf(accessory_price), 1)
      } else {
        this.accessoryCheck.splice(index, 1, true)
        // this.accessoryCheck[index] = true
        this.accessoryId.push(accessory_id)
        this.accessoryName.push(accessory_name)
        this.accessoryPrice.push(accessory_price)
      }
      console.log(this.accessoryId)
    },
    showAccessoryMore() {
      if (this.showMoreAccessory) {
        this.showMoreAccessory = false
      } else {
        this.showMoreAccessory = true
      }
    },
    pjCheck(index) {
      this.accessoryCheck.splice(index, 1, !this.accessoryCheck[index])
      if (!this.accessoryCheck[index]) {
        this.accessoryId.splice(this.accessoryId.indexOf(this.accessoryList[index].id), 1)
        this.accessoryName.splice(this.accessoryName.indexOf(this.accessoryList[index].name), 1)
        this.accessoryPrice.splice(this.accessoryPrice.indexOf(this.accessoryList[index].price), 1)
      } else {
        this.accessoryId.push(this.accessoryList[index].id)
        this.accessoryName.push(this.accessoryList[index].name)
        this.accessoryPrice.push(this.accessoryList[index].price)
      }
      this.defaultPj = ''
      for (let i = 0; i < this.accessoryCheck.length; i++) {
        if (this.accessoryCheck[i]) {
          this.defaultPj += this.accessoryList[i].name + ' '
        }
      }
      if (this.defaultPj === '') {
        this.defaultPj = '请选择配件'
      }
    },
    goGoodsCompare() {
      if (this.checked.length === 0) {
        this.$toast('尚未选择对比商品，请先选择！')
        return
      }
      this.$router.push({
        name: 'GoodsCompare',
        params: {
          address: this.address
        }
      })
    },
    // 获取配件信息
    getAccessoryList() {
      accessoryList.data = { e_product_id: this.spId }
      this.asyncApiModule(accessoryList).then(res => {
        this.accessoryList = res
        if (this.accessoryList.length > 0) {
          if (this.accessoryList.length > 5) {
            this.showMore = true
          }
          this.sfzspj = true
          for (var i = 0; i < this.accessoryList.length; i++) {
            this.accessoryCheck.push(false)
          }
        }
      })
        .catch(() => {
          this.loading = false
        })
    },
    gysChange(gys) {
      this.defaultGys = gys.supplier_name
      this.xzGys = gys.id
      this.show2 = false
    },
    // 获取商品介绍和规格包装信息
    getSpjsAndGgbz() {
      getProduct_textInfo.data = { e_product_id: this.spId }
      this.asyncApiModule(getProduct_textInfo).then(res => {
        this.pro_intro = res.e_product_text.int_text
        this.ggbz = res.e_product_text.sku_text
        this.wareqd = res.e_product_text.wareqd
      })
        .catch(() => {
          this.loading = false
        })
    },
    // 获取浏览历史商品信息列表
    getMyfavoriteList() {
      getMyfavoriteList.data = { e_product_id: this.spId }
      this.asyncApiModule(getMyfavoriteList).then(res => {
        this.myfavoriteList = res
      })
        .catch(() => {
          this.loading = false
        })
    },
    // 跳转对应商品详情请页
    goGoodsdetail(name, id) {
      this.$store.commit('goods/SET_SPID', id)
      this.$store.commit('goods/SET_SPMC', name)
      this.$router.go(0)
      window.scrollTo(0, 0)
    },
    // 获取相似商品列表
    getComparisionProductList() {
      getComparisionProductList.data = { name: this.$store.state.goods.spmc, id: this.$store.state.goods.spid }
      this.asyncApiModule(getComparisionProductList).then(res => {
        this.comparisionProductList = res
        // for (const comparision of this.comparisionProductList) {
        //   if (this.check.indexOf(comparision.product_id) !== -1) {
        //     this.similarCheck.push(true)
        //   } else {
        //     this.similarCheck.push(false)
        //   }
        //   this.similarId.push(comparision.product_id)
        // }
      })
        .catch(() => {
          this.loading = false
        })
    },
    goAddAddress() {
      this.$router.push({
        name: 'AddressAdd',
        params: {
          title: '新增收货地址'
        }
      })
    },
    addressChange(address, index) {
      this.addressIndex = index
      this.show1 = false
      this.defaultAddress = address.area + '/' + address.address
    },
    // 获取收货地址
    getAdressList() {
      this.loading = true
      getAddressList.data = { pageno: 1,
        pagerow: 1000000 }
      this.asyncApiModule(getAddressList).then(res => {
        this.addresList = res.list
        for (let i = 0; i < this.addresList.length; i++) {
          const address = this.addresList[i]
          if (address.default_address === 1) {
            this.defaultAddress = address.area + '/' + address.address
            this.addressIndex = i
          }
        }
      })
        .catch(() => {
          this.loading = false
        })
    },
    // 获取评价和成交记录接口
    getOrderDetail() {
      getOrderDetailList.data = { e_product_id: this.spId }
      this.asyncApiModule(getOrderDetailList).then(res => {
        this.orderDetailList = res.orderDetailList
        this.orderDetailListShow = this.orderDetailList.concat().splice(0, 5)
        this.cjjlList = res.cjjlList
        // this.maxPrice = res.maxPrice
        // this.avgPrice = res.avgPrice
        // this.minPrice = res.minPrice
      })
        .catch(() => {
          this.loading = false
        })
    },
    // 获取供应商列表
    getGysList() {
      getSupplierList.data = { e_product_id: this.spId }
      this.asyncApiModule(getSupplierList).then(res => {
        this.gysList = res
      })
        .catch(() => {
          this.loading = false
        })
    },
    // 获取商品详情
    getSpxx() {
      getProductInfo.data = { e_product_id: this.spId }
      this.asyncApiModule(getProductInfo).then(res => {
        this.gysmc = res.gysmc
        this.spxx = res.e_product
        this.slide = res.imgsList
        if (res.e_product.dsbs === 'KJXY') {
          this.getGysList()
        }
      }).catch((error) => {
        this.$toast(error)
      })
    },
    goBack() {
      this.$router.back()
    },
    // 控制配件显示/收起更多
    showChooseMore() {
      this.showChoose = !this.showChoose
    },
    showPopupDz() {
      this.show1 = true
    },
    showPopupDz1() {
      this.show2 = true
    },
    goevaluationlist() {
      this.$router.push({
        name: 'EvaluationList',
        params: {
          pjList: this.orderDetailList
        }
      })
    },
    showPopupGys() {
      this.showGys = true
    },
    showPopupSku() {
      this.showSku = true
    },
    showPopupImage() {
      this.showImage = true
    },
    goRecommendlist() {
      this.$router.push({
        name: 'RecommendList'
      })
    },
    goGoodsList() {
      this.$router.push({
        name: 'GoodsList'
      })
    },
    goBuyCart() {
      this.$store.commit('mall/SET_ISACTIVE', 2)
      this.$router.push({
        name: 'BuyCart'
      })
    },
    async goConfirmOrder(spid) {
      if (!spid) {
        this.$toast('商品信息错误，购买失败')
        return
      }
      var productInfo = {
        province: this.address.province,
        city: this.address.city,
        county: this.address.county,
        town: this.address.town,
        produceList: [],
        dsbs: this.spxx.dsbs
      }
      productInfo.produceList.push({
        skuId: this.spId,
        num: 1
      })
      let state = true
      await this.$store.dispatch('goods/getLessStockAction', productInfo).then(res => {
        for (var key in res) {
          if (key !== 'success') {
            this.$toast(res[key])
            state = false
          }
        }
      }).catch(() => {
        this.loading = false
      })
      if (!state) {
        return
      }
      if (this.spxx && this.spxx.dsbs === 'KJXY' && this.gysList) {
        if (this.xzGys) {
          const obj = this.gysList.find((item) => {
            return item.id === this.xzGys
          })
          const query = {
            e_product_id: encrypt(this.spId),
            num: encrypt(this.cgsl),
            gysbh: encrypt(this.xzGys),
            gysmc: encrypt(encodeURIComponent(obj.supplier_name)),
            accessoryIdsList: encrypt(this.accessoryId),
            accessoryName: this.accessoryName,
            accessoryPrice: encrypt(this.accessoryPrice)
          }
          this.$store.commit('bid/SET_XJXX', '')
          this.$store.commit('goods/SET_QUERY', query)
          this.$store.commit('goods/SET_CHECKCART', '')
          this.$router.push({
            name: 'ConfirmOrder',
            params: {
              address: this.addresList[this.addressIndex]
            }
          })
        } else {
          this.$toast('请选择供应商！')
        }
      } else {
        const query = {
          e_product_id: encrypt(this.spId),
          num: encrypt(this.cgsl),
          accessoryIdsList: encrypt(this.accessoryId),
          accessoryName: this.accessoryName,
          accessoryPrice: encrypt(this.accessoryPrice)
        }
        this.$store.commit('bid/SET_XJXX', '')
        this.$store.commit('goods/SET_QUERY', query)
        this.$store.commit('goods/SET_CHECKCART', '')
        this.$router.push({
          name: 'ConfirmOrder',
          params: {
            address: this.addresList[this.addressIndex]
          }
        })
      }
    },
    // 点击进行锚点定位
    goTarget(index) {
      var name = '#section-' + (index + 1)// 需要跳转的id
      const target = document.querySelector('body')
      var anchor = this.$el.querySelector(name)
      // 判断滚动条是否滚动到底部
      if (target.scrollHeight <= target.scrollTop + target.clientHeight) {
        this.activeStep = index
      }
      document.documentElement.scrollTop = anchor.offsetTop - 60
    }
  }
}
</script>

